import React from "react";
import { useDrag } from "react-dnd";
import { MachineStore } from "../../../../MachineStore/MachineStore";
import { ChannelModel } from "../../../../Models/ChannelMapModel";
import { MachineInstanceModel } from "../../../../Models/MachineInstanceModel";
import { Constants } from "../../../Constants";
import { DragItemTypes } from "../../../DragItemTypes";
import { Channel } from "./Channel/Channel";
import { Machine } from "./Machine/Machine";
import { Props } from "./Props";

const MachineInstance: React.FC<Props> = (props) => {
    const [, drag] = useDrag({
        item: { type: DragItemTypes.MachineInstance, id: props.MachineInstance.Id },
        end: (dropResult, monitor) => {
            if (!monitor.didDrop()) {
                props.OnRemoved(props.MachineInstance.Id);
            }
        },
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging(),
        }),
    });

    const OnChannelChanged = (channel: ChannelModel, index: number) => {
        const machineInstance: MachineInstanceModel = {
            Cell: props.MachineInstance.Cell,
            MachineId: props.MachineInstance.MachineId,
            Channels: props.MachineInstance.Channels.map((c, i) => (i === index) ? channel : c),
            InputChannels: props.MachineInstance.InputChannels,
            Id: props.MachineInstance.Id
        };

        props.OnChanged(machineInstance);
    };

    const channels = props.MachineInstance.Channels.map((c, index) => <div key={index}><Channel OnChanged={(channelMap) => OnChannelChanged(channelMap, index)} ChannelMap={c} /></div>);
    const absoluteStyle: React.CSSProperties = {
        position: "absolute",
    };
    const machineSize: React.CSSProperties = { height: Constants.CellSize - 2, width: Constants.CellSize - 2, margin: 1 };

    const machineType = MachineStore.GetStore().GetMachineType(props.MachineInstance.MachineId);
    return (
        <div ref={drag} style={machineSize}>
            <div style={absoluteStyle}>
                <Machine Machine={machineType} />
            </div>
            {channels}
        </div>

    );
};

export default MachineInstance;
