export const FormatMessage = (content: string): string => {
    let formattedMessage: string | undefined;

    if (content === "") {
        formattedMessage = "<Empty>";
    } else {
        formattedMessage = content;
        let seekPosition = 0;

        while (true) {
            seekPosition = formattedMessage.indexOf("<", seekPosition);
            if (seekPosition === -1) {
                break;
            }

            const lastIndex = formattedMessage.indexOf(">", seekPosition);
            formattedMessage = formattedMessage.substring(0, seekPosition) + "?" + formattedMessage.substring(lastIndex + 1);
        }
    }
    return formattedMessage;
};
