import React from "react";
import NumberInput from "../NumberInput/NumberInput";
import { Props } from "./Props";

export const Dashboard: React.FC<Props> = (props) => {

    return (
        <div>
            <label title="Step time in tenths of seconds, lower for faster animations">Step Time</label>
            <NumberInput value={props.StepTime} valueChanged={props.OnStepTimeChanged} />
        </div>
    );
};