import React from "react";
import { OutputMessageModel } from "../../../Models/OutputMessageModel";
import { OutputStatus } from "../../../Models/OutputStatusEnum";
import { MessageDisplay } from "./MessageDisplay/MessageDisplay";
import { Props } from "./Props";

export const TestDisplay: React.FC<Props> = (props) => {

    const OnClick = () => {
        props.OnPlay(props.Test);
    };

    const inputs = props.Test.inputs;
    const outputs = props.Test.outputs;
    let messages: JSX.Element[] = [];
    let index = 0;
    for (let i = 0; i < inputs.length; i++) {
        messages.push(<MessageDisplay Message={inputs[i]} Input={true} key={index++} Status={OutputStatus.NotChecked}/>);
        const expectedOutputs = outputs.filter((o) => o.Order === i);
        let actuals: OutputMessageModel[] | undefined;
        if (props.ActualOutputs !== undefined) {
            actuals = props.ActualOutputs.filter((o) => o.Order === i);
        }
        for (let j = 0; j < expectedOutputs.length; j++) {
            const expected = expectedOutputs[j];
            let valid = OutputStatus.NotChecked;
            if (actuals !== undefined) {
                const indexActual = (actuals.findIndex((o) => o.Channel === expected.Channel && o.Content === expected.Content));
                if (indexActual > -1) {
                    valid = OutputStatus.Fullfilled;
                    actuals.splice(indexActual, 1);
                } else {
                    valid = OutputStatus.NotFullfilled;
                }
            }
            messages = messages.concat(<MessageDisplay Message={expectedOutputs[j]} Input={false} key={index++} Status={valid} />);
        }

        if (actuals !== undefined) {
            // eslint-disable-next-line
            messages = messages.concat(actuals.map((o) => <MessageDisplay Message={o} Input={false} key={index++} Status={OutputStatus.NotMatched} />));
        }
    }

    const style: React.CSSProperties = { display: "flex", flexDirection: "row", padding: 0, borderBottom: "solid 0.5px", margin: 5 };
    const verticalStyle: React.CSSProperties = { display: "flex", flexDirection: "column", flexGrow: 1 };

    return (
        <div style={style}>
            <button onClick={OnClick}>Play</button>
            <div style={verticalStyle}>
                {messages}
            </div>
        </div>
    );
};