import React from "react";
import { useDrop } from "react-dnd";
import { MachineId } from "../../../MachineStore/MachineId";
import { Constants } from "../../Constants";
import { DragItemTypes } from "../../DragItemTypes";
import { FormatMessage } from "../../MessageFormatter";
import MachineInstance from "./MachineInstance/MachineInstance";
import { Props } from "./Props";

const Cell: React.FC<Props> = (props) => {

    const [, drop] = useDrop({
        accept: [DragItemTypes.MachineInstance, DragItemTypes.NewMachine],
        drop: (item, monitor) => {
            switch (item.type) {
                case DragItemTypes.MachineInstance:
                    props.OnMovedMachine(monitor.getItem().id, props.location);
                    break;
                case DragItemTypes.NewMachine:
                    const itemProps = monitor.getItem() as { id: MachineId };
                    props.OnNewMachine(itemProps.id, props.location);
                    break;
            }
        },
    });

    const style: React.CSSProperties = {
        height: Constants.CellSize,
        width: Constants.CellSize,
        border: "1px solid #999",
        float: "left",
        marginTop: -1,
        marginRight: -1,
        position: "relative"
    };

    let formattedMessage: string | undefined;
    if (props.message !== undefined) {
        formattedMessage = FormatMessage(props.message.Content);
    }

    return (
        <div style={style} ref={drop}>
            {formattedMessage !== undefined && formattedMessage}
            {props.machineInstance !== undefined ? <MachineInstance MachineInstance={props.machineInstance} OnChanged={props.OnChanged} OnRemoved={props.OnMachineRemoved} /> : ""}

        </div >
    );
};

export default Cell;