import React from "react";
import { GridSizeSetting } from "./GridSizeSetting/GridSizeSetting";
import { Props } from "./Props";
import { SaveMenu } from "./SaveMenu/SaveMenu";

export class Settings extends React.Component<Props> {

    public render() {
        return (
            <div>
                <div>
                    <SaveMenu Setup={this.props.Setup} OnSetupChanged={this.props.OnSetupChanged} OnShowingSetup={this.props.OnShowingSetup} ShowSetup={this.props.ShowSetup} />
                    <GridSizeSetting Setup={this.props.Setup} OnSetupChanged={this.props.OnSetupChanged} />
                </div>
            </div>
        );
    }
}