import React from "react";
import { GridSetupModel } from "../../../Models/GridSetupModel";
import NumberInput from "../../NumberInput/NumberInput";
import { Props } from "./Props";

export const GridSizeSetting: React.FC<Props> = (props) => {
    const columnsChanged = (value: number) => {
        const newGridSetup: GridSetupModel = {
            NumberOfColumns: value,
            NumberOfRows: props.Setup.NumberOfRows,
            MachineInstances: props.Setup.MachineInstances
        };

        props.OnSetupChanged(newGridSetup);
    };

    const rowsChanged = (value: number) => {
        const newGridSetup: GridSetupModel = {
            NumberOfColumns: props.Setup.NumberOfColumns,
            NumberOfRows: value,
            MachineInstances: props.Setup.MachineInstances
        };

        props.OnSetupChanged(newGridSetup);
    };

    return (
        <div>
            <label>Rows:</label>
            <NumberInput value={props.Setup.NumberOfRows} valueChanged={rowsChanged} />
            <label>Columns:</label>
            <NumberInput value={props.Setup.NumberOfColumns} valueChanged={columnsChanged} />
        </div>
    );
};