import { AddStarMachine } from "./DefaultMachines/AddStarMachine";
import { AddZeroMachine } from "./DefaultMachines/AddZeroMachine";
import { CompareMachine } from "./DefaultMachines/CompareMachine";
import { CopyMachine } from "./DefaultMachines/CopyMachine";
import { DeleteMachine } from "./DefaultMachines/DeleteMachine";
import { GreenStartMachine } from "./DefaultMachines/GreenStartMachine";
import { IncrementMachine } from "./DefaultMachines/IncrementMachine";
import { MoveMachine } from "./DefaultMachines/MoveMachine";
import { RedStartMachine } from "./DefaultMachines/RedStartMachine";
import { RotateMachine } from "./DefaultMachines/RotateMachine";
import { IActor } from "./IActor";
import { IMachine } from "./IMachine";
import { IMachineType } from "./IMachineType";
import { MachineId } from "./MachineId";

export class MachineStore {
    private static machineStore: MachineStore;
    private machines: IMachine[];

    private constructor() {
        this.machines = [];
        this.init();
    }

    public static GetStore(): MachineStore {
        if (MachineStore.machineStore === undefined)        {
            MachineStore.machineStore = new MachineStore();
        }

        return MachineStore.machineStore;
    }

    private init(): void {
        this.machines = [];
        this.machines.push(new GreenStartMachine());
        this.machines.push(new RedStartMachine());
        this.machines.push(new MoveMachine());
        this.machines.push(new CopyMachine());
        this.machines.push(new DeleteMachine());
        this.machines.push(new RotateMachine());
        this.machines.push(new CompareMachine());
        this.machines.push(new AddStarMachine());
        this.machines.push(new AddZeroMachine());
        this.machines.push(new IncrementMachine());
        // this.machines.push(new SplitMachine());
    }

    private FindMachine(id: MachineId): IMachine {
        const machine = this.machines.find((m) => m.Id.Id === id.Id && m.Id.Type === id.Type);
        if (machine === undefined) {
            throw new Error("No machine found with id " + id.Id + " and type " + id.Type);
        }

        return machine;
    }

    public GetActor(id: MachineId): IActor {
        return this.FindMachine(id).GetActor();
    }

    public GetMachineType(id: MachineId): IMachineType {
        return this.FindMachine(id).GetType();
    }

    public GetAllMachine(): IMachine[] {
        return this.machines;
    }
}