import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { LevelModel } from "../../Models/LevelModel";

export const GetType: LevelModel = {
    AllowedMachines: [1, 5, 6, 7, 8, 9, 10, 11, 12],
    Description: "The machine should exit green is the first character is a number, exit red if it is a letter and exit blue otherwise",
    Name: "GetType",
    Tests: [
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "42" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "42" }]
        },
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "this starts with a letter" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Red, Content: "this starts with a letter" }]
        },
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: ",#@%!" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Blue, Content: ",#@%!" }]
        },
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Blue, Content: "" }]
        }
    ]
};