import React from "react";
import { ChannelColorEnum } from "../../../Models/ChannelColorsEnum";
import { MessageModel } from "../../../Models/MessageModel";
import { OutputStatus } from "../../../Models/OutputStatusEnum";
import { MessageDisplay } from "../TestDisplay/MessageDisplay/MessageDisplay";
import { EditableMessageDisplay } from "./EditableMessageDisplay/EditableMessageDisplay";
import { Props } from "./Props";
import { State } from "./State";

export class CustomTest extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.InputChanged = this.InputChanged.bind(this);
        this.AddInput = this.AddInput.bind(this);
        this.DeleteInput = this.DeleteInput.bind(this);
        this.Play = this.Play.bind(this);

        this.state = { Inputs: [{ Content: "", Channel: ChannelColorEnum.Green }], Outputs: [] };
    }

    private InputChanged(index: number, message: MessageModel) {
        const inputs = this.state.Inputs;
        inputs[index] = message;
        this.setState({ Inputs: inputs });
    }

    private AddInput(): void {
        const message: MessageModel = { Channel: ChannelColorEnum.Green, Content: "" };
        const inputs = this.state.Inputs;
        inputs.push(message);
        this.setState({ Inputs: inputs });
    }

    private DeleteInput(index: number): void {
        const inputs = this.state.Inputs;
        inputs.splice(index, 1);
        this.setState({ Inputs: inputs });
    }

    private Play(): void {
        this.props.PlayTest(this.state.Inputs).then((outputs) => {
            this.setState({ Outputs: outputs });
        }).catch(() => {
            this.setState({ Outputs: [] });
        });
    }

    public render() {
        let inputs: JSX.Element[] = [];
        let lineNumber = 0;
        for (let i = 0; i < this.state.Inputs.length; i++) {
            inputs.push(<div key={lineNumber++} >
                <EditableMessageDisplay
                    Message={this.state.Inputs[i]}
                    AllowedInputColors={this.props.AllowedInputColors}
                    MessageChanged={(message: MessageModel) => this.InputChanged(i, message)} />
                {this.state.Inputs.length > 1 && <button onClick={() => this.DeleteInput(i)}>Delete</button>}
            </div>);
            let outputs: JSX.Element[] = [];
            const outputsForThisInput = this.state.Outputs.filter((o) => o.Order === i);
            // eslint-disable-next-line
            outputs = outputsForThisInput.map((o, index) => <MessageDisplay Message={o} Input={false} key={lineNumber++} Status={OutputStatus.NotChecked} />);
            inputs = inputs.concat(outputs);
        }
        return (
            <div>
                <button onClick={this.Play}>Play</button>
                <div>
                    {inputs}
                    {this.props.MultipleInputs &&
                        <button onClick={this.AddInput}>Add input</button>}
                </div>
            </div>
        );
    }
}