import React from "react";
import { GridSetupModel } from "../../../Models/GridSetupModel";
import { Props } from "./Props";
import { State } from "./State";

export class SaveMenu extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.ShowSetupString = this.ShowSetupString.bind(this);
        this.LoadSetup = this.LoadSetup.bind(this);
        this.SetupStringChanged = this.SetupStringChanged.bind(this);

        this.state = { SetupString: "", NewSetupString: "" };
    }
    public render() {
        const verticalFlex: React.CSSProperties = { display: "flex", flexFlow: "column wrap" };
        const margin: React.CSSProperties = { display: "flex", flexDirection: "column" };
        return (
            <div style={verticalFlex}>
                <div style={margin}>
                    <button title={"Show setup string, copy it so you can later restore your setup"} onClick={this.ShowSetupString}>Show setup string</button>
                    {this.props.ShowSetup && <input value={this.state.SetupString} />}
                </div>
                <div style={margin}>
                    <label>Load Setup</label>
                    <input value={this.state.NewSetupString} onChange={this.SetupStringChanged} />
                    {this.state.NewSetupString !== "" && <button title={"Load Setup, after having pasted a setup above, click to load this setup"} onClick={this.LoadSetup}>Load setup</button>}
                </div>
            </div>
        );
    }

    private SetupStringChanged(event: React.ChangeEvent<HTMLInputElement>): void {
        this.setState({ NewSetupString: event.target.value });
    }

    private ShowSetupString(): void {
        this.setState({ SetupString: JSON.stringify(this.props.Setup) });
        this.props.OnShowingSetup();
    }

    private LoadSetup(): void {
        const setupModel = JSON.parse(this.state.NewSetupString) as GridSetupModel;
        if (setupModel !== undefined) {
            this.props.OnSetupChanged(setupModel);
            this.setState({ NewSetupString: "" });
        }
    }
}
