import React from "react";
import { ChannelColorEnum } from "../../../../Models/ChannelColorsEnum";
import { Props } from "./Props";

export const EditableMessageDisplay: React.FC<Props> = (props) => {
    const onMessageChanged = (event: React.ChangeEvent<HTMLInputElement>) => {
        props.MessageChanged({ Content: event.target.value, Channel: props.Message.Channel });
    };

    const onChannelChanged = (event: React.ChangeEvent<HTMLSelectElement>) => {
        props.MessageChanged({ Content: props.Message.Content, Channel: event.target.value as ChannelColorEnum });
    };

    return (
        <div style={{ display: "flex", flexDirection: "row" }}>
            {props.AllowedInputColors.length > 1 ? <select value={props.Message.Channel} style={{ backgroundColor: props.Message.Channel }} onChange={onChannelChanged}>
                {props.AllowedInputColors.map((cc, index) => <option value={cc} key={index} style={{ backgroundColor: cc }}></option>)}
            </select> : <div style={{ backgroundColor: props.Message.Channel, width: 10 }} />}
            <input value={props.Message.Content} onChange={onMessageChanged}></input>
        </div>
    );
};