import React from "react";
import { MachineStore as Store } from "../../MachineStore/MachineStore";
import { Constants } from "../Constants";
import { Props } from "./Props";
import StoredMachine from "./StoredMachine/StoredMachine";

const MachineStore: React.FC<Props> = (props) => {

    const machines = Store.GetStore().GetAllMachine();
    const relativeStyle: React.CSSProperties = {
        position: "relative",
        height: Constants.CellSize,
        width: Constants.CellSize
    };

    const filteredMachines = machines.filter((m) => !props.disallowedMachines.some((mi) => mi.Id === m.Id.Id && mi.Type === m.Id.Type));
    const storedMachines = filteredMachines.map((m, index) => <div style={relativeStyle} key={index}><StoredMachine machine={m} /></div>);

    return (
        <div style={{ marginLeft: 50 }}>
            {storedMachines}
        </div>
    );
};

export default MachineStore;