import { LevelModel } from "../Models/LevelModel";
import { AppendLength } from "./Levels/AppendLength";
import { GetLength } from "./Levels/GetLength";
import { GetType } from "./Levels/GetType";
import { IsEmpty } from "./Levels/IsEmpty";
import { KeepNumbers } from "./Levels/KeepNumbers";
import { PlusOne } from "./Levels/PlusOne";
import { RemoveAllRepeats } from "./Levels/RemoveAllRepeats";
import { RotateBackwards } from "./Levels/RotateBackwards";
import { Sandbox } from "./Levels/Sandbox";

export class LevelStore {
    private static levelStore: LevelStore;
    private levels: LevelModel[];

    private constructor() {
        this.levels = [];
        this.init();
    }

    public static GetStore(): LevelStore {
        if (LevelStore.levelStore === undefined) {
            LevelStore.levelStore = new LevelStore();
        }

        return LevelStore.levelStore;
    }

    private init(): void {
        this.levels.push(Sandbox);
        this.levels.push(IsEmpty);
        this.levels.push(RemoveAllRepeats);
        this.levels.push(GetType);
        this.levels.push(KeepNumbers);
        this.levels.push(GetLength);
        this.levels.push(AppendLength);
        this.levels.push(RotateBackwards);
        this.levels.push(PlusOne);
    }

    public GetAllLevels(): LevelModel[] {
        return this.levels;
    }
}