import { Props } from "./Props";
import React from "react";
import { LevelPreview } from "./LevelPreview/LevelPreview";
import { LevelStore } from "../../Levels/LevelStore";

const LevelList: React.FC<Props> = (props) => {
    const levelStore = LevelStore.GetStore();

    const levels = levelStore.GetAllLevels().map((l, index) => <LevelPreview Level={l} LevelClicked={props.OnLevelClicked} key={index}/>)

    return (
        <div>
            <b>Challenges:</b>
            {levels}
        </div>
    )
}

export default LevelList;
