import React from "react";
import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { Constants } from "../../../../Constants";
import { Props } from "./Props";

export class Channel extends React.Component<Props> {

    constructor(props: Props) {
        super(props);
        this.onClicked = this.onClicked.bind(this);
    }
    public render() {

        const cellSize = Constants.CellSize - 2;
        const width = 19;
        const height = 4;
        const border = 1;
        const channelStyle: React.CSSProperties = {
            position: "absolute",
            height: cellSize / 50 * height,
            width: cellSize / 50 * width,
            marginLeft: cellSize / 50 * (25 + (height + border) / 2),
            marginTop: cellSize / 50 * (25 - (height + border) / 2),
            backgroundColor: this.props.ChannelMap.Channel,
            borderStyle: "solid",
            borderWidth: border + "px",
            pointerEvents: "visible"
        };

        let rotate: string = "";
        switch (this.props.ChannelMap.Direction) {
            case DirectionEnum.Down:
                rotate = "rotate(90deg)";
                break;
            case DirectionEnum.Left:
                rotate = "rotate(180deg)";
                break;
            case DirectionEnum.Up:
                rotate = "rotate(270deg)";
                break;
            case DirectionEnum.Right:
                rotate = "rotate(0deg)";
                break;

            default:
                break;
        }

        const rotation: React.CSSProperties = {
            transform: rotate,
            height: cellSize,
            width: cellSize,
            position: "absolute",
            pointerEvents: "none"
        };

        return (
            <div style={rotation}>
                <div style={channelStyle} onClick={this.onClicked}></div>
            </div>
        );
    }

    public onClicked(event: React.MouseEvent<HTMLDivElement, MouseEvent>): void {
        if (this.props.OnChanged !== undefined) {
            let newDirection: DirectionEnum = DirectionEnum.Left;
            switch (this.props.ChannelMap.Direction) {
                case DirectionEnum.Down:
                    newDirection = DirectionEnum.Left;
                    break;
                case DirectionEnum.Left:
                    newDirection = DirectionEnum.Up;
                    break;
                case DirectionEnum.Up:
                    newDirection = DirectionEnum.Right;
                    break;
                case DirectionEnum.Right:
                    newDirection = DirectionEnum.Down;
                    break;
            }

            this.props.OnChanged({ Direction: newDirection, Channel: this.props.ChannelMap.Channel });
        }
    }
}