import React from "react";
import { Props } from "./Props";
import { State } from "./State";

export class MenuItem extends React.Component<Props, State> {
    /**
     *
     */
    constructor(props: Props) {
        super(props);
        this.showItem = this.showItem.bind(this);
        this.state = { Showing: false };
    }

    private showItem() {
        this.setState({ Showing: !this.state.Showing });
    }

    public render() {
        const style: React.CSSProperties = { borderLeft: "solid 0.5px", marginLeft: 10, padding: 10 };
        return (
            <div style={{ marginLeft: 10 }}>
                <button onClick={this.showItem}>{this.state.Showing ? "Hide " + this.props.ItemName : "Show " + this.props.ItemName}</button>
                {this.state.Showing &&
                    <div style={style}>
                        {this.props.children}
                    </div>}
            </div>
        );
    }
}