import { IActor } from "../MachineStore/IActor";
import { MachineStore } from "../MachineStore/MachineStore";
import { CellModel } from "../Models/CellModel";
import { ChannelColorEnum } from "../Models/ChannelColorsEnum";
import { DirectionEnum } from "../Models/DirectionEnum";
import { GridSetupModel } from "../Models/GridSetupModel";
import { MachineInstanceModel } from "../Models/MachineInstanceModel";
import { MessageLocationModel } from "../Models/MessageLocationModel";
import { MessageModel } from "../Models/MessageModel";

export class Computer {
    private setup: GridSetupModel;
    private machines: Array<{ id: number, actor: IActor }> = [];
    private store: MachineStore;

    constructor(setup: GridSetupModel) {
        this.setup = setup;
        this.store = MachineStore.GetStore();
        this.GetMachines();
    }

    public NewSetup(setup: GridSetupModel) {
        this.setup = setup;
        this.GetMachines();
    }

    private GetMachines(): void {
        this.machines = this.setup.MachineInstances.map((mi) => ({ id: mi.Id, actor: this.store.GetActor(mi.MachineId) }));
    }

    public GetStartMachines() {
        const startMachines: MachineInstanceModel[] = [];
        for (const machineInstance of this.setup.MachineInstances) {
            if (machineInstance.MachineId.Id < 4 && machineInstance.MachineId.Type === "Default") {
                startMachines.push(machineInstance);
            }
        }

        return startMachines;
    }

    public GetStartMachine(color: ChannelColorEnum): MachineInstanceModel | undefined {

        let id: number = 0;
        switch (color) {
            case ChannelColorEnum.Green:
                id = 0;
                break;
            case ChannelColorEnum.Red:
                id = 1;
                break;
            case ChannelColorEnum.Blue:
                id = 2;
                break;
            case ChannelColorEnum.Yellow:
                id = 3;
                break;
        }

        return this.setup.MachineInstances.find((mi) => mi.MachineId.Id === id && mi.MachineId.Type === "Default");

    }

    public StartComputation(input: MessageModel): MessageLocationModel[] {
        const messageLocations: MessageLocationModel[] = [];

        const machineInstance = this.GetStartMachine(input.Channel);
        if (machineInstance === undefined) {
            return [];
        }

        const direction = machineInstance.Channels[0].Direction;
        const messageLocation: MessageLocationModel = { Content: input.Content, Direction: direction, Cell: machineInstance.Cell };
        messageLocations.push(messageLocation);

        return messageLocations;
    }

    public GetNextMessages(messages: MessageLocationModel[]): MessageLocationModel[] {
        const nextMessages: MessageLocationModel[] = [];
        for (const message of messages) {
            let machine: MachineInstanceModel | undefined;
            let messageCell: CellModel | undefined = message.Cell;

            while (machine === undefined) {
                messageCell = this.Move(messageCell!, message.Direction);
                if (messageCell === undefined) {
                    break;
                }

                // eslint-disable-next-line
                machine = this.setup.MachineInstances.find((mi) => mi.Cell.Column === messageCell!.Column && mi.Cell.Row === messageCell!.Row);
            }

            if (messageCell === undefined) {
                nextMessages.push({ Cell: undefined, Direction: message.Direction, Content: message.Content })
                continue;
            }

            const channel = ChannelColorEnum.Green;

            const actor = this.machines.find((m) => m.id === machine!.Id)!.actor;
            const nextContents = actor.Act({ Content: message.Content, Channel: channel });
            for (const nextContent of nextContents) {
                const channelMap = machine!.Channels.find((cm) => cm.Channel === nextContent.Channel);
                if (channelMap === undefined) {
                    throw Error("Channelmap " + nextContent.Channel + "undefined");
                }

                const messageLocation: MessageLocationModel = { Cell: machine!.Cell, Content: nextContent.Content, Direction: channelMap.Direction };
                nextMessages.push(messageLocation);
            }
        }

        return nextMessages;
    }

    private Move(cell: CellModel, direction: DirectionEnum): CellModel | undefined {
        switch (direction) {
            case DirectionEnum.Down:
                if (cell.Row === this.setup.NumberOfRows - 1) {
                    return;
                }
                return { Column: cell.Column, Row: cell.Row + 1 };
            case DirectionEnum.Up:
                if (cell.Row === 0) {
                    return;
                }
                return { Column: cell.Column, Row: cell.Row - 1 };
            case DirectionEnum.Right:
                if (cell.Column === this.setup.NumberOfColumns - 1) {
                    return;
                }
                return { Column: cell.Column + 1, Row: cell.Row };
            case DirectionEnum.Left:
                if (cell.Column === 0) {
                    return;
                }
                return { Column: cell.Column - 1, Row: cell.Row };
        }
    }
}