import React from "react";
import { Props } from "./Props";

const NumberInput: React.FC<Props> = (props) => {
    const IncreaseValue: () => void = () => {
        props.valueChanged(props.value + 1);
    };
    const DecreaseValue: () => void = () => {
        props.valueChanged(props.value - 1);
    };

    const style: React.CSSProperties = { marginLeft: 5, marginRight: 5 };

    return (<div>
        <button style={style} onClick={DecreaseValue}>-</button>
        <label >{props.value}</label>
        <button style={style} onClick={IncreaseValue}>+</button>
    </div>);
};

export default NumberInput;