import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { MessageModel } from "../../Models/MessageModel";
import { IActor } from "../IActor";
import { IMachine } from "../IMachine";
import { IMachineType } from "../IMachineType";
import { MachineId } from "../MachineId";

export class CopyMachine implements IMachine {

    private actor: IActor = {
        Act: (message) => {
            const returnValue: MessageModel[] = [];
            const content = message.Content.length > 0 ? message.Content[0] + message.Content : "";

            returnValue.push({ Content: content, Channel: ChannelColorEnum.Green });
            return returnValue;
        }
    };
    public GetActor(): IActor {
        return this.actor;
    }
    public GetType(): IMachineType {
        return {
            BorderColor: "green",
            Color: "yellow",
            Name: "Copy",
            InputChannels: [],
            OutputChannels: [ChannelColorEnum.Green],
            Description: "Copies the first character of the string and puts it in front of the string"
        };
    }
    public Id: MachineId = {
        Id: 6,
        Type: "Default"
    };
}
