import React from "react";
import { MachineId } from "../../MachineStore/MachineId";
import { MachineStore } from "../../MachineStore/MachineStore";
import { CellModel } from "../../Models/CellModel";
import { ChannelModel } from "../../Models/ChannelMapModel";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { GridSetupModel } from "../../Models/GridSetupModel";
import { MachineInstanceModel } from "../../Models/MachineInstanceModel";
import { MessageLocationModel } from "../../Models/MessageLocationModel";
import Cell from "./Cell/Cell";
import { GridProps } from "./Props";

export class Grid extends React.Component<GridProps> {
    private store: MachineStore;
    constructor(props: GridProps) {
        super(props);
        this.ChannelChanged = this.ChannelChanged.bind(this);
        this.MachineMoved = this.MachineMoved.bind(this);
        this.MachineAdded = this.MachineAdded.bind(this);
        this.MachineRemoved = this.MachineRemoved.bind(this);

        this.store = MachineStore.GetStore();
    }

    public render() {
        const rows = [];
        for (let rowIndex = 0; rowIndex < this.props.GridSetup.NumberOfRows; rowIndex++) {
            const row = [];
            for (let columnIndex = 0; columnIndex < this.props.GridSetup.NumberOfColumns; columnIndex++) {
                let foundMachineInstance: MachineInstanceModel | undefined;
                let foundMessage: MessageLocationModel | undefined;

                for (const machineInstance of this.props.GridSetup.MachineInstances) {
                    if (machineInstance.Cell.Column === columnIndex && machineInstance.Cell.Row === rowIndex) {
                        foundMachineInstance = machineInstance;
                    }
                }

                for (const message of this.props.Messages) {
                    if (message.Cell!.Column === columnIndex && message.Cell!.Row === rowIndex) {
                        foundMessage = message;
                    }
                }

                row.push(<Cell
                    key={columnIndex}
                    machineInstance={foundMachineInstance}
                    message={foundMessage}
                    OnChanged={this.ChannelChanged}
                    OnMovedMachine={this.MachineMoved}
                    OnNewMachine={this.MachineAdded}
                    OnMachineRemoved={this.MachineRemoved}
                    location={{ Row: rowIndex, Column: columnIndex }} />);
            }
            rows.push(<div key={rowIndex}>{row}</div>);
        }

        return (
            <div>
                {rows}
            </div>

        );
    }

    private ChannelChanged(machineInstance: MachineInstanceModel) {
        const newGridSetup: GridSetupModel = {
            NumberOfColumns: this.props.GridSetup.NumberOfColumns,
            NumberOfRows: this.props.GridSetup.NumberOfRows,
            MachineInstances: this.props.GridSetup.MachineInstances.map((mi) =>
                (mi.Cell.Column === machineInstance.Cell.Column && mi.Cell.Row === machineInstance.Cell.Row) ?
                    machineInstance
                    :
                    mi)
        };

        this.props.OnChanged(newGridSetup);
    }

    private MachineMoved(id: number, location: CellModel): void {
        const index = this.props.GridSetup.MachineInstances.findIndex((mi) => mi.Id === id);
        this.props.GridSetup.MachineInstances[index].Cell = location;
        this.props.OnChanged(this.props.GridSetup);
    }
    private MachineRemoved(id: number): void {
        this.props.GridSetup.MachineInstances = this.props.GridSetup.MachineInstances.filter((mi) => mi.Id !== id);
        this.props.OnChanged(this.props.GridSetup);
    }
    private MachineAdded(id: MachineId, location: CellModel): void {
        const machine = this.store.GetMachineType(id);
        const channels: ChannelModel[] = [];
        let direction: DirectionEnum.Up = DirectionEnum.Up;
        for (const color of machine.OutputChannels) {
            channels.push({ Channel: color, Direction: direction });
            direction++;
        }

        const maxId = (this.props.GridSetup.MachineInstances.length === 0) ? 0 : Math.max(...this.props.GridSetup.MachineInstances.map((mi) => mi.Id));

        const machineInstance: MachineInstanceModel = {
            Cell: location,
            MachineId: id,
            Channels: channels,
            InputChannels: [],
            Id: maxId + 1
        };
        this.props.GridSetup.MachineInstances.push(machineInstance);
        this.props.OnChanged(this.props.GridSetup);
    }
}