import React from "react";
import { Constants } from "../../../../Constants";
import { Props } from "./Props";

export class Machine extends React.Component<Props> {
    public render() {
        const machineSize = Constants.CellSize / 5 * 3;
        const borderSize = machineSize / 10;

        const machineStyle: React.CSSProperties = {
            // position: "absolute",
            height: machineSize,
            width: machineSize,
            backgroundColor: this.props.Machine.Color,
            borderStyle: "solid",
            borderColor: this.props.Machine.BorderColor,
            borderWidth: borderSize,
            borderRadius: "50%",
            marginLeft: ((Constants.CellSize - machineSize) / 2 - borderSize),
            marginTop: ((Constants.CellSize - machineSize) / 2 - borderSize)
        };

        return (
            <div style={machineStyle} />
        );
    }
}