import React from "react";
import { Computer } from "../../Computing/Computer";
import { LoadGridSetup } from "../../Computing/Setup";
import { Sandbox } from "../../Levels/Levels/Sandbox";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { GridSetupModel } from "../../Models/GridSetupModel";
import { LevelModel } from "../../Models/LevelModel";
import { MessageLocationModel } from "../../Models/MessageLocationModel";
import { MessageModel } from "../../Models/MessageModel";
import { OutputMessageModel } from "../../Models/OutputMessageModel";
import { Constants } from "../Constants";
import { Dashboard } from "../Dashboard/Dashboard";
import { Grid } from "../Grid/Grid";
import { LevelDisplay } from "../LevelDisplay/LevelDisplay";
import LevelList from "../LevelList/LevelList";
import MachineStore from "../MachineStore/MachineStore";
import { MenuItem } from "../MenuItem/MenuItem";
import { Settings } from "../Settings/Settings";
import { State } from "./State";

export class Game extends React.Component<{}, State> {
    private computer: Computer;
    private inputs: MessageModel[] = [];
    private outputs: OutputMessageModel[] = [];
    private cancelComputation: (() => void) | undefined = undefined;
    constructor(props: object) {
        super(props);
        this.inputChanged = this.inputChanged.bind(this);
        this.startComputation = this.startComputation.bind(this);
        this.CalculateNextMessages = this.CalculateNextMessages.bind(this);
        this.GridSetupChanged = this.GridSetupChanged.bind(this);
        this.LoadSetup = this.LoadSetup.bind(this);
        this.StepTimeChanged = this.StepTimeChanged.bind(this);
        this.OnShowingSetup = this.OnShowingSetup.bind(this);
        this.stopComputation = this.stopComputation.bind(this);
        this.handleKeyPress = this.handleKeyPress.bind(this);
        this.LevelSelected = this.LevelSelected.bind(this);
        this.PlayTest = this.PlayTest.bind(this);

        const gridSetup = LoadGridSetup();
        this.computer = new Computer(gridSetup);
        this.state = {
            Computing: false,
            Messages: [],
            GridSetup: gridSetup,
            CustomInput: "",
            StepTime: 6,
            ShowingSetup: false,
            SelectedLevel: Sandbox
        };
    }

    private inputChanged(e: React.ChangeEvent<HTMLInputElement>): void {
        let text = e.target.value;
        text = text.replace("*", "");
        text = text.replace("<", "");
        text = text.replace(">", "");

        this.setState({ CustomInput: text });
    }
    private startComputation(): Promise<OutputMessageModel[]> {
        return new Promise<OutputMessageModel[]>((resolve, reject) => {
            this.stopComputation().then(() => {
                this.outputs = [];
                return this.handleNextInput(0).then(resolve).catch(reject);
            });
        });
    }

    private handleNextInput(inputNumber: number): Promise<OutputMessageModel[]> {

        if (this.inputs.length <= inputNumber) {
            this.setState({ Computing: false });
            return new Promise<OutputMessageModel[]>((resolve) => {
                resolve(this.outputs);
                this.cancelComputation = undefined;
            });

        } else {
            const messageModels: MessageLocationModel[] = this.state.Messages;
            const nextMessages = messageModels.concat(this.computer.StartComputation(this.inputs[inputNumber]));

            this.setState({ Computing: true, Messages: nextMessages });
            return this.SetDelay(this.CalculateNextMessages, inputNumber);
        }
    }

    private SetDelay<T>(callback: (args: any) => Promise<T>, args: any): Promise<T> {
        return new Promise<T>((resolve, reject) => {
            const clearHandle = window.setTimeout(() => callback(args).then(resolve).catch(reject), this.state.StepTime * 100);
            this.cancelComputation = () => {
                window.clearTimeout(clearHandle);
                reject("Cancelled");
            };
        });
    }

    private stopComputation(): Promise<void> {
        return new Promise((resolve) => {
            if (this.cancelComputation) {
                this.cancelComputation();
                this.cancelComputation = undefined;
            }
            this.setState({ Messages: [], Computing: false }, () => resolve());
        });
    }

    private CalculateNextMessages(inputNumber: number): Promise<OutputMessageModel[]> {

        const messages = this.computer.GetNextMessages(this.state.Messages);

        const doneMessages = messages.filter((mm) => mm.Cell === undefined);
        const outputs: OutputMessageModel[] = [];
        for (const doneMessage of doneMessages) {
            switch (doneMessage.Direction) {
                case DirectionEnum.Up:
                    outputs.push({ Content: doneMessage.Content, Channel: ChannelColorEnum.Green, Order: inputNumber });
                    break;
                case DirectionEnum.Down:
                    outputs.push({ Content: doneMessage.Content, Channel: ChannelColorEnum.Red, Order: inputNumber });
                    break;
                case DirectionEnum.Right:
                    outputs.push({ Content: doneMessage.Content, Channel: ChannelColorEnum.Blue, Order: inputNumber });
                    break;
                case DirectionEnum.Left:
                    outputs.push({ Content: doneMessage.Content, Channel: ChannelColorEnum.Yellow, Order: inputNumber });
                    break;
            }
        }
        if (doneMessages.length > 0) {
            const newOutputs = this.outputs.concat(outputs);
            this.outputs = newOutputs;
        }

        const nextMessages = messages.filter((mm) => mm.Cell !== undefined);
        this.setState({ Messages: nextMessages });
        if (nextMessages.length > 0) {
            return this.SetDelay(this.CalculateNextMessages, inputNumber);
        } else {
            return this.handleNextInput(inputNumber + 1);
        }
    }

    private LoadSetup(event: React.ChangeEvent<HTMLInputElement>): void {
        const newValue = JSON.parse(event.target.value);
        const newSetup = newValue as GridSetupModel;
        if (newSetup !== undefined) {
            this.GridSetupChanged(newSetup);
        }
    }

    public componentDidMount(): void {
        document.addEventListener("keyup", (event) => this.handleKeyPress(event), false);
    }

    public componentWillUnmount() {
        document.removeEventListener("keyup", (event) => this.handleKeyPress(event), false);
    }

    public render() {
        const startMachines = this.computer.GetStartMachines();

        const disallowedStartMachines = startMachines.map((m) => m.MachineId);
        disallowedStartMachines.push({ Id: 1, Type: "Default" });
        // const allowedInputColors = [ChannelColorEnum.Green, ChannelColorEnum.Red, ChannelColorEnum.Blue];
        const allowedInputColors = [ChannelColorEnum.Green];

        const height = this.state.GridSetup.NumberOfRows * (Constants.CellSize + 1);
        const width = this.state.GridSetup.NumberOfColumns * (Constants.CellSize + 1);
        const gridSizeStyle: React.CSSProperties = { display: "flex", height, width, borderStyle: "solid", borderColor: "green blue red yellow", borderWidth: 5 };

        return (
            <div style={{ display: "flex", flexFlow: "column wrap" }}>
                <div>
                    <h2 style={{ textAlign: "center" }}>LogicBoxes</h2>
                </div>
                <div style={{ display: "flex", flexFlow: "row wrap" }}>
                    <div style={{ flex: "1 1 200px" }}>
                        <MenuItem ItemName="Settings">
                            <Settings
                                Setup={this.state.GridSetup}
                                OnSetupChanged={this.GridSetupChanged}
                                ShowSetup={this.state.ShowingSetup}
                                OnShowingSetup={this.OnShowingSetup} />
                        </MenuItem>
                        <MenuItem ItemName="Challenges">
                            <LevelList OnLevelClicked={this.LevelSelected} />
                        </MenuItem>
                        <LevelDisplay Level={this.state.SelectedLevel} PlayTest={this.PlayTest} MultipleInputsAllowed={false} AllowedInputs={allowedInputColors} />

                    </div>
                    <div style={{ display: "flex", flex: "4 1 400px", flexFlow: "column", alignItems: "center" }}>
                        <div style={gridSizeStyle}>
                            <Grid
                                GridSetup={this.state.GridSetup}
                                Computing={this.state.Computing}
                                Messages={this.state.Messages}
                                OnChanged={this.GridSetupChanged} />
                        </div>
                        <div>
                            <Dashboard StepTime={this.state.StepTime} OnStepTimeChanged={this.StepTimeChanged} />
                        </div>
                    </div>
                    <div style={{ flex: "1 1 200px" }}>
                        <MachineStore disallowedMachines={disallowedStartMachines} />
                    </div>
                </div>
            </div>
        );
    }

    private PlayTest(input: MessageModel[]): Promise<OutputMessageModel[]> {
        this.inputs = input;
        return this.startComputation();
    }

    private GridSetupChanged(newGridSetup: GridSetupModel): void {
        this.computer.NewSetup(newGridSetup);
        this.setState({ GridSetup: newGridSetup, ShowingSetup: false });
    }

    private StepTimeChanged(newStepTime: number): void {
        this.setState({ StepTime: newStepTime });
    }

    private OnShowingSetup() {
        this.setState({ ShowingSetup: true });
    }

    private LevelSelected(level: LevelModel) {
        this.setState({ SelectedLevel: level });
    }

    private handleKeyPress(event: KeyboardEvent): void {
        switch (event.key) {
            case "Enter":
                this.startComputation();
                break;
            case " ":
                this.stopComputation();
                break;
            default:
                break;
        }
    }
}