import React from "react";
import { useDrag } from "react-dnd";
import { ChannelModel } from "../../../Models/ChannelMapModel";
import { DirectionEnum } from "../../../Models/DirectionEnum";
import { Constants } from "../../Constants";
import { DragItemTypes } from "../../DragItemTypes";
import { Channel } from "../../Grid/Cell/MachineInstance/Channel/Channel";
import { Machine } from "../../Grid/Cell/MachineInstance/Machine/Machine";
import { Props } from "./Props";

const StoredMachine: React.FC<Props> = (props) => {
    const [, drag] = useDrag({
        item: { type: DragItemTypes.NewMachine, id: props.machine.Id },
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging(),
        }),
    });

    const machineType = props.machine.GetType();
    const channels: ChannelModel[] = [];
    let direction: DirectionEnum.Up = DirectionEnum.Up;
    for (const color of machineType.OutputChannels) {
        channels.push({ Channel: color, Direction: direction });
        direction++;
    }

    const channelComponents = channels.map((c, index) => <Channel key={index} ChannelMap={c} />);
    const absoluteStyle: React.CSSProperties = {
        position: "absolute",

        left: Constants.CellSize / 3
    };
    const labelStyle: React.CSSProperties = {
        position: "absolute"
    };
    return (
        <div ref={drag} title={machineType.Description}>

            <div style={absoluteStyle}>
                <Machine Machine={props.machine.GetType()} />
            </div>
            <div style={absoluteStyle}>
                {channelComponents}
            </div>
            <label style={labelStyle}>{machineType.Name}</label>
        </div>);
};

export default StoredMachine;