import { ChannelColorEnum } from "../Models/ChannelColorsEnum";
import { DirectionEnum } from "../Models/DirectionEnum";
import { GridSetupModel } from "../Models/GridSetupModel";

let GridSetup: GridSetupModel;

export const LoadGridSetup = (): GridSetupModel => {
    if (GridSetup === undefined) {
        GridSetup = LoadSetup();
    }

    return GridSetup;
};

const LoadSetup = (): GridSetupModel => ({
    NumberOfColumns: 6,
    NumberOfRows: 5,
    MachineInstances: [{
        Cell: { Row: 2, Column: 0 },
        MachineId: { Id: 0, Type: "Default" },
        Channels: [{ Direction: DirectionEnum.Right, Channel: ChannelColorEnum.Green }],
        InputChannels: [], Id: 1
    }]
});
