import React from "react";
import { OutputMessageModel } from "../../Models/OutputMessageModel";
import { TestModel } from "../../Models/TestModel";
import { CustomTest } from "./CustomTest/CustomTest";
import { Props } from "./Props";
import { State } from "./State";
import { TestDisplay } from "./TestDisplay/TestDisplay";

export class LevelDisplay extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.testStarted = this.testStarted.bind(this);
        this.state = { ActualOutputs: [] };
    }
    private testStarted(test: TestModel, index: number) {
        this.props.PlayTest(test.inputs).then((outputs: OutputMessageModel[]) => {
            const actualOutputs = this.state.ActualOutputs;
            actualOutputs[index] = outputs;
            this.setState({ ActualOutputs: actualOutputs });
        }).catch(() => {
            this.setState({ ActualOutputs: [] });
        });
    }

    public render() {
        const tests = this.props.Level.Tests.map((t, index) => <TestDisplay Test={t} OnPlay={(test: TestModel) => this.testStarted(test, index)} key={index} ActualOutputs={this.state.ActualOutputs[index]} />);
        const style: React.CSSProperties = { padding: 10 };

        return (
            <div style={style}>
                <b>{this.props.Level.Name}</b>
                <br />
                <i>{this.props.Level.Description}</i>
                {this.props.Level.Tests.length === 0 && <CustomTest PlayTest={this.props.PlayTest} AllowedInputColors={this.props.AllowedInputs} MultipleInputs={this.props.MultipleInputsAllowed} />}
                {tests}
            </div>
        );
    }
}