import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { LevelModel } from "../../Models/LevelModel";

export const GetLength: LevelModel = {
    AllowedMachines: [1, 5, 6, 7, 8, 9, 10, 11, 12],
    Description: "The machine should return the length of the input string",
    Name: "GetLength",
    Tests: [
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "four" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "4" }]
        },
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "aabccd" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "6" }]
        },
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "1" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "1" }]
        },
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "0" }]
        },
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "This is hard" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "12" }]
        },
        {
            inputs: [{ Channel: ChannelColorEnum.Green, Content: "This might even be harder" }],
            outputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "25" }]
        }
    ]
};